* {
  box-sizing: border-box; }

html, body {
  width: 100%;
  height: 100%; }

body {
  font-family: 'Montserrat';
  font-weight: 400;
  font-size: 14px;
  color: #8e8c8c;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased; }

.container {
  margin: 0 auto; }

.wrapper {
  overflow: hidden;
  min-height: 100%; }
  .wrapper:after {
    content: "";
    display: block;
    height: 634px; }

.footer {
  height: 634px;
  margin-top: -634px; }

ul {
  padding: 0;
  margin: 0; }

ul li {
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: normal; }

